/* Style */

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  padding: 20px 70px;
  border-bottom-left-radius: 35px;
  border-top-right-radius: 35px;
  background-color: blue;
  height: 70px;
  border: none;
}

.header-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-nav h2 {
  padding: 0 10px;
  color: #fff;
}

.logo {
  width: 45px;
}

.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav li {
  list-style: none;
  padding: 0 15px;
}

.nav li a {
  color: #fff;
  text-decoration: none;
}

.nav li a:hover {
  color: red;
  font-size: 18px;
  transition: all 1s;
}
