*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    font-family: Arial, Helvetica, sans-serif;
}

.main-section{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 25px 0 0 0;
    background-color: olive;
    margin-top: 20px;
    color: #fff;
    border: none;
}

.main-section p{
    padding: 20px 0 0 0;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    width: 90%;
    color: #fff;

}

.boxing{
    display: flex;
    justify-content: center;
    flex-direction: row;
    align-items: center;
    width: 100%;
}

.box{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 40px 25px 8px 25px;
}

.box img{
    width: 80px;
}

.box p{
    width: 200px;
    height: 60px;
    color: #fff;
}

.footer{
    display: flex;
    align-items: center;
    margin-top: 35px;
    border: none;
    height: 62px;
    background: rgba(0, 0, 0, 0.4);
}

.footer h3{
    color: black;
    font-size: 22px;
    margin: 0 auto;
}